.social-style-container {
    padding: 12px;
}

.header .title {
    font-size: 30px;
    font-weight: 900;
}

.header .sub-title {
    font-size: 16px;
    color: #AAAAAA;
    margin: 10px 0 20px;
}

.topic {
    margin-top: 5px;
    border: 1px transparent dashed;
}

.warnning-border {
    -webkit-animation: flash 1s ease-in-out 5;
    animation: flash 1s ease-in-out 5;
}

@-webkit-keyframes flash{
    from {
        border: 1px #ff2f2f dashed;
    }
    to {
        border: 1px transparent dashed;
    }
}
@keyframes flash{
    from {
        border: 1px #ff2f2f dashed;
    }
    to {
        border: 1px transparent dashed;
    }
}

.box .title {
    transform: skewX(-10deg);
    color: #338dd4;
    margin-bottom: 5px;
    text-align: center;
    padding: 2px 0 2px;
    font-weight: 600;
    border-radius: 2px;
    width: 30px;
    box-shadow: 2px 0 4px 0 rgba(97, 153, 181, 0.52);
}

.box .item.active {
    font-weight: 600;
}

.box .item.active .icon {
    position: relative;
    width: 10px;
    height: 10px;
    border: 1px #a2a2a2 solid;
    border-radius: 50%;
    display: inline-block;
    margin-right: 5px;
}

.box .item.active .icon::after {
    position: absolute;
    display: block;
    content: '';
    width: 6px;
    height: 6px;
    background-color: #a2a2a2;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.box .item.ordinary .icon {
    width: 10px;
    height: 10px;
    border: 1px #a2a2a2 solid;
    border-radius: 50%;
    display: inline-block;
    margin-right: 5px;
}

.footer {
    margin: 20px 0 40px;
    padding: 10px;
}

.footer .title {
    font-size: 18px;
}

.footer .title>span {
    font-size: 20px;
    font-weight: 900;
}

.footer .tip {
    margin-top: 10px;
    font-size: 15px;
    padding: 5px 0;
    color: #338dd4;
}

.btn {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 10px 0;
    text-align: center;
    width: 100%;
    background-color: #338dd4;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: 5px;
    font-size: 20px;
}

.result {
    margin-top: 10px;
    color: #525252;
}

.toast {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 60%;
    border-radius: 4px;
    padding: 10px 8px;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    background-color: rgba(0,0,0,0.7);
    transform: translate(-50%, -50%);
}

.result-detail-box {
    margin-top: 10px;
}

.result-detail-box .title {
    display: inline-block;
    font-size: 18px;
    font-weight: 900;
    border-radius: 4px 0 4px 0;
    text-align: center;
    padding: 2px 6px;
    color: #ffffff;
    background-image: linear-gradient(45deg, #82b8d2, #2196F3);
}

.result-detail-box .subtitle {
    position: relative;
    display: inline-block;
    font-size: 16px;
    font-weight: 900;
    padding-left: 16px;
    color: #525252;
    padding-bottom: 4px;
}

.result-detail-box .subtitle::after {
    position: absolute;
    display: block;
    content: '';
    width: 8px;
    height: 1px;
    background-color: #525252;
    border-radius: 2px;
    left: 4px;
    top: 50%;
    margin-top: -2px; 
}

.result-detail-box .detail-container>div {
    margin-top: 6px;
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 5px);
    margin-right: 10px;
}

.result-detail-box .detail-container>div:nth-child(2n) {
    margin-right: 0;
}

.result-detail-box .detail-container .top {
    background-color: #cecece;
    border-radius: 4px 4px 0 0;
    padding: 4px;
    text-align: center;
}

.result-detail-box .detail-container .bottom {
    padding: 4px;
    height: 130px;
    border: 1px solid #cecece;
}
